<template>
  <div class="groups">
    <h2>Groups</h2>
    <div class="filters">
      <label for="text-filter">Search</label>
      <input type="text" class="name-filter" v-model="textFilter" id="text-filter">
      <label for="sort-reversed">Reversed</label>
      <input type="checkbox" class="reverse-sort" v-model="sortReversed"  id="sort-reversed">
    </div>
    <div class="group-list">
      <template v-if="groups && groups.length > 0">
        <template v-for="group in groupsSortedAndFiltered">
          <pm2-group-listing :group="group"></pm2-group-listing>
        </template>
      </template>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import PM2GroupListing from './PM2GroupListing'

  export default {
    data () {
      return {
        textFilter: '',
        sortReversed: true
      }
    },
    computed: {
      groups () {
        return this.$store.state.piecemaker.groups
      },
      groupsSortedAndFiltered () {
        let clonedGroup = this.groups.slice(0)
        if (this.textFilter) {
          let textFilterNoCase = this.textFilter.toLowerCase()
          clonedGroup = clonedGroup.filter((g) => {
            return g.title.toLowerCase().indexOf(textFilterNoCase) >= 0 ||
              g.description.toLowerCase().indexOf(textFilterNoCase) >= 0
          })
        }
        clonedGroup.sort((a, b) => {
          return a.title.localeCompare(b.title)
        })
        if (this.sortReversed) {
          clonedGroup.reverse()
        }
        return clonedGroup
      }
    },
    mounted () {
      Vue.pm2Service.getGroups()
    },
    components: {
      'pm2-group-listing': PM2GroupListing
    }
  }
</script>

<style lang="stylus" scoped>
  .groups

    h2
      font-weight bold
      margin-bottom 1em

    .filters
      margin-bottom 1em

    .group-list
      display flex
      width 100%
      flex-wrap wrap

</style>



