import Vue from 'vue'
import Router from 'vue-router'
import PM2Groups from '../components/PM2Groups'
import PM2Group from '../components/PM2Group'
import PM2Context from '../components/PM2Context'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: PM2Groups
    },
    {
      path: '/group/:groupId',
      component: PM2Group,
      children: [
        {
          path: 'context/:contextId',
          component: PM2Context
        }
      ]
    }
  ]
})



