<template>
  <div class="event-listing">
    <div class="time">{{event.utc_timestamp | contextTime(context)}}</div>
    <div class="labels">
      <span class="label">{{event.type}}</span>
      <template v-for="tag in tags">
        <span class="label">{{tag}}</span>
      </template>
    </div>
    <h4 class="title"><slot name="title">{{title}}</slot></h4>
  </div>
</template>

<script>
export default {
  props: ['event', 'context'],
  data () {
    return {}
  },
  computed: {
    title () {
      return this.event.fields.title || 'Untitled event'
    },
    tags () {
      let tags = this.event.fields.tags
      if (tags && tags.length > 0) return this.event.fields.tags.split(',')
      return []
    }
  }
}
</script>

<style lang="stylus" scoped>

  .event-listing

    border 1px solid lightgray
    border-bottom-width 0px
    padding 0.5em
    overflow hidden

    &:last-child
      border-bottom-width 1px

    &:hover
      background-color #eee

    .labels
      float left

    .label
      float left
      border 1px solid dodgerblue
      border-radius 1em
      padding 0.2em 0.75em 0.2em 0.75em
      color darkslateblue
      background-color lightskyblue
      font-size 0.75rem
      text-align center
      margin-right 0.15em

    .label:last-child
      margin-right 0em

    .time
      float left
      font-size 0.75rem
      background-color #eee
      color dimgrey
      padding 0.25em 0.75em 0.25em 0.75em
      margin-right 0.5em

    .title
      float left
      margin 0 0 0 1em
      padding 0

</style>



