<template>
  <div class="group-listing">
    <div class="inner">
      <router-link :to="{path: 'group/'+group.id}">
        <h3>{{group.title}}</h3>
        <p>{{group.description}}</p>
      </router-link>
    </div>
  </div>
</template>

<script>
  export default {
    props: [
      'group'
    ]
  }
</script>

<style lang="stylus" scoped>

  .group-listing
    flex-basis 25%

    .inner
      border 1px solid lightgray
      margin 0.2em
      padding 0.5em

    .inner:hover
      border-color grey
      background-color #eee

    h3
      margin 0

    p
      font-size 0.8rem
      color grey

    p:first-child
      padding-top 0px

    p:last-child
      padding-bottom 0px

</style>



