<template>
  <div>
    <template v-if="group && context">
      <template v-if="events && events.length > 0">
          <template v-for="event in events">
            <pm2-event-listing :event="event" :context="context"></pm2-event-listing>
          </template>
      </template>
    </template>
  </div>
</template>

<script>
  import Vue from 'vue'
  import PM2EventListing from './PM2EventListing'

  export default {
    props: ['group', 'context'],
    data () {
      return {}
    },
    computed: {
      events () {
        return this.group.events.filter((e) => e.id !== this.context.id)
      }
    },
    mounted () {
      if (this.group.id !== this.context.event_group_id) {
        throw new Error('Piecemaker2-Context component: context needs to belong to group')
      }
      Vue.pm2Service.getEventsForEvent(this.group.id, this.context)
    },
    components: {
      'pm2-event-listing': PM2EventListing
    }
  }
</script>

<style>

</style>



