<template>
  <div class="group">
    <template v-if="group">
      <h2 class="title">
        <router-link :to="{path: '/'}">Groups</router-link> /
        <router-link :to="{path: groupPath}">{{group.title}}</router-link>
        <template v-if="context">
          / <router-link :to="{path: contextPath}">{{context.fields.title}}</router-link>
        </template>
      </h2>
      <p>{{group.description}}</p>
      <template v-if="context">
        <!-- display context -->
        <pm2-context :group="group" :context="context"></pm2-context>
      </template>
      <template v-else-if="videos && videos.length > 0">
        <!-- a video listing -->
        <template v-for="video in videos">
          <pm2-event-listing :event="video">
            <router-link :to="{path: group.id+'/context/'+video.id}" slot="title">{{video.fields.title}}</router-link>
          </pm2-event-listing>
        </template>
      </template>
      <template v-else>
        No videos â€¦
      </template>
    </template>
  </div>
</template>

<script>
  import Vue from 'vue'
  import PM2Context from './PM2Context'
  import PM2EventListing from './PM2EventListing'

  export default {
    data () {
      return {
        groupId: -1
      }
    },
    computed: {
      groupPath () {
        return '/group/' + this.group.id
      },
      contextPath () {
        return this.groupPath + '/context/' + this.context.id
      },
      contextId () {
        return parseInt(this.$route.params.contextId) || undefined
      },
      context () {
        if (this.videos) {
          return this.videos.find((v) => v.id === this.contextId)
        }
        return undefined
      },
      group () {
        let tmpGroup = this.$store.state.piecemaker.groups.find((g) => g.id === this.groupId)
        if (tmpGroup && !tmpGroup.videos) Vue.pm2Service.getVideos(this.groupId)
        return tmpGroup
      },
      videos () {
        return this.group.videos
      }
    },
    watch: {
      groupId () {
        if (!this.group) {
          Vue.pm2Service.getGroup(this.groupId)
        } else if (!this.group.videos) {
          Vue.pm2Service.getVideos(this.groupId)
        }
      }
    },
    mounted () {
      this.groupId = parseInt(this.$route.params.groupId, 10)
    },
    components: {
      'pm2-context': PM2Context,
      'pm2-event-listing': PM2EventListing
    }
  }
</script>

<style lang="stylus" scoped>
.group
  .title
    font-weight bold
</style>



