/**
 * This is a VueJS 2.0 plugin that wraps the Piecemaker (2) api client
 */

import PieceMakerApi from 'piecemaker-api-client'
import Vue from 'vue'
import moment from 'moment'

const pm2Module = {
  state: {
    user: {},
    groups: []
  },
  mutations: {
    user (state, user) {
      state.user = user
    },
    groups (state, groups) {
      state.groups = groups
    },
    group (state, group) {
      let oldGroup = state.groups.find((g) => g.id === group.id)
      if (oldGroup) {
        let oldGroupIndex = state.groups.indexOf(oldGroup)
        group.videos = group.videos || []
        group.events = group.events || []
        Vue.set(state.groups, oldGroupIndex, group)
      }
    },
    videos (state, {groupId, videos}) {
      let videoGroup = state.groups.find((g) => g.id === groupId)
      if (videoGroup) {
        Vue.set(videoGroup, 'videos', videos)
      }
    },
    event (state, {groupId, event}) {
      let eventGroup = state.groups.find((g) => g.id === groupId)
      if (eventGroup) {
        if (eventGroup.events) {
          let oldEvent = eventGroup.events.find((e) => e.id === event.id)
          if (oldEvent) {
            let oldEventIndex = eventGroup.events.indexOf(oldEvent)
            Vue.set(eventGroup.events, oldEventIndex, event)
          } else {
            eventGroup.events.push(event)
            eventGroup.events.sort((a, b) => a.utc_timestamp.getTime() - b.utc_timestamp.getTime()) // sort by date/time!
          }
        } else {
          eventGroup.events = [event]
        }
      }
    },
    events (state, {groupId, events}) {
      let eventGroup = state.groups.find((g) => g.id === groupId)
      if (eventGroup) {
        let eventGroupIndex = state.groups.indexOf(eventGroup)
        Vue.set(state.groups, eventGroupIndex, Object.assign({}, eventGroup, {events: events}))
      }
    }
  }
  /* can we use getters to populate the state? */
}

class PM2ApiService {
  store
  constructor (pm2) {
    this.pm2 = pm2
    this.pm2.error = (err) => {
      console.log('Catching errs in PM2 plugin â€¦')
      throw new Error(err)
    }
  }
  getPlugin () {
    return store => {
      this.store = store
      store.subscribe(({type, payload}, state) => {
        console.log('[pm2Service]', type)
      })
      store.registerModule('piecemaker', pm2Module)
    }
  }
  /* these could also be store actions */
  login (email, password, next = () => {}) {
    this.pm2.login(email, password, (apiKey) => {
      this.store.commit('user', {email, password, api_key: apiKey}, {module: 'piecemaker'})
      next(apiKey)
    })
  }
  getGroups (next = () => {}) {
    this.pm2.listGroups((groups) => {
      this.store.commit('groups', groups, {module: 'piecemaker'})
      next(groups)
    })
  }
  getGroup (groupId, next = () => {}) {
    if (this.store.state.piecemaker.groups.length > 0) {
      this.pm2.getGroup(groupId, (group) => {
        this.store.commit('group', group, {module: 'piecemaker'})
        next(group)
      })
    } else {
      this.getGroups(() => {
        this.getGroup(groupId, next)
      })
    }
  }
  getVideos (groupId, next = () => {}) {
    this.pm2.listEventsOfType(groupId, 'video', (videos) => {
      this.store.commit('videos', {groupId, videos}, {module: 'piecemaker'})
      next(videos)
    })
  }
  getEvent (groupId, eventId, next = () => {}) {
    this.pm2.getEvent(groupId, eventId, (event) => {
      this.store.commit('event', {groupId, event}, {module: 'piecemaker'})
      next(event)
    })
  }
  getEventsForEvent (groupId, event, next = () => {}) {
    let from = event.utc_timestamp.getTime()
    let to = from + (event.duration * 1000.0)
    this.pm2.listEventsForTimespan(groupId, from, to, 'intersect', (events) => {
      this.store.commit('events', {groupId, events}, {module: 'piecemaker'})
      next(events)
    })
  }
}

export default {
  install (Vue, options) {
    // the service
    Vue.pm2Service = new PM2ApiService(new PieceMakerApi(options))
    // filters
    Vue.filter('contextTime', function (time, context = false) {
      if (context) {
        let diffTime = time.getTime() - context.utc_timestamp.getTime()
        return diffTime
      }
      return moment(time).format('YYYY-MM-DD HH:mm')
    })
  }
}



